

























import Vue from "vue";
import { Component } from "vue-property-decorator";
import Tabs from "@/components/Tabs.vue";
import recordTypeList from "@/constants/recordTypeList";
import dayjs from "dayjs";
import day from "dayjs";
import clone from "@/lib/clone";
import Chart from '@/components/Chart.vue';
import _ from 'lodash'
@Component({
  components: { Tabs,Chart }
})
export default class Statistics extends Vue {
  tagString(tags: Tag[]) {
    return tags.length === 0 ? "无" : tags.map(t => t.name).join("，");
  }
  mounted(){
    const div=(this.$refs.chartWrapper as HTMLDivElement);
    div.scrollLeft=div.scrollWidth;
  }
  beautify(string: string) {
    const day = dayjs(string);
    const now = dayjs();
    if (day.isSame(now, "day")) {
      return "今天";
    } else if (day.isSame(now.subtract(1, "day"), "day")) {
      console.log("hi");
      return "昨天";
    } else if (day.isSame(now.subtract(2, "day"), "day")) {
      return "前天";
    } else if (day.isSame(now, "year")) {
      return day.format("M月D日");
    } else {
      return day.format("YYYY年M月D日");
    }
  }
  get keyValueList(){
    const today=new Date();
    const array=[];
    for(let i=0;i<=29;i++)
    {
      const dateString=day(today).subtract(i,'day').format('YYYY-MM-DD');
      const found=_.find(this.groupedList,{title:dateString});
      array.push({date:dateString,value: found?found.total:0});
      console.log(dateString);
      console.log(found);
    }

    array.sort((a,b)=>{
      if (a.date>b.date)
      {
        return 1;
      }
      else if (a.date===b.date)
      {
        return 0;
      }
      else
      {
        return -1;
      }
    });
    return array;
  }
  get chartOptions(){

    const keys=this.keyValueList.map(item=>item.date);
    const values=this.keyValueList.map(item=>item.value);

    return {
  grid:{
    left:10,
    top:16,
    right:10,
    bottom:26
  },
  xAxis: {
    axisTick:{
      alignWithLabel:true
    },
    axisLine:{
      lineStyle:{color:'#666'}
    },
    type: 'category',
    data: keys,
    axisLabel:{
      formatter:function(value: string,index: number){
        return value.substr(5)
      }
    }

  },
  yAxis: {
    type: 'value',
    show:false
  },
  series: [{
    symbol:'circle',
    symbolSize:10,
    itemStyle:{borderWidth:1,color:'#666'},
    data:values,
    type: 'line'
  }],
  tooltip:{show:true,
    triggerOn:'click',
    position:'top',
    formatter:'{c}'
  }
}
  }
  get recordList() {
    return (this.$store.state as RootState).recordList;
  }
  get groupedList() {
    const { recordList } = this;

    const newList = clone(recordList)
      .filter((r: { type: string }) => r.type === this.type)
      .sort(
        (
          a: { createdAt: string | number | Date | dayjs.Dayjs | undefined },
          b: { createdAt: string | number | Date | dayjs.Dayjs | undefined }
        ) => dayjs(b.createdAt).valueOf() - dayjs(a.createdAt).valueOf()
      );

    if (newList.length === 0) {
      return [];
    }

    type Result = { title: string; total?: number; items: RecordItem[] }[];
    // if(newList.length===0){return [] Result;}

    const result: Result = [
      {
        title: dayjs(newList[0].createdAt).format("YYYY-MM-DD"),
        items: [newList[0]]
      }
    ];
    for (let i = 1; i < newList.length; i++) {
      const current = newList[i];
      const last = result[result.length - 1];
      if (dayjs(last.title).isSame(dayjs(current.createdAt), "day")) {
        last.items.push(current);
      } else {
        result.push({
          title: dayjs(current.createdAt).format("YYYY-MM-DD"),
          items: [current]
        });
      }
    }
    result.map(group => {
      group.total = group.items.reduce((sum, item) => {
        // console.log(sum);
        //console.log(item);
        return sum + item.amount;
      }, 0);
    });
    return result;
  }
  beforeCreate() {
    this.$store.commit("fetchRecords");
  }
  type = "-";
  recordTypeList = recordTypeList;
}
